import React from 'react';
import Sketch from 'react-p5';

let len=200,angle=0;
const changeangle=(e)=>{
    angle=e.target.value;
   
}

const branch=(p5,len)=>{

    p5.stroke(255);
    p5.line(0,0,0,-len);
    p5.translate(0,-len)
    if(len>3){
        p5.push()
        p5.rotate(angle);
        branch(p5,len*0.75);
        p5.pop()
        p5.push()
        p5.rotate(-angle);
        branch(p5,len*0.75);
        p5.pop()

    }

}
const setup=(p5,parent)=>{
    p5.createCanvas(7*(p5.displayWidth/8),7*(p5.displayWidth/16)).parent(parent);
    angle=330;
    len=p5.height/4;
    p5.angleMode(p5.DEGREES);
    p5.background(0);   
    p5.translate(p5.width/2,p5.height) 
    
    
    
    }
const draw=(p5)=>{
    p5.background(0);   
    p5.stroke(255);
    p5.translate(p5.width/2,p5.height) 
    branch(p5,len);


}



export default function Recursivetree() {
    return (
        <div>
            <center><h2>Recursive Tree</h2></center><br />
            <label>change angle value:</label>
             <input id="typeinp" type="range" min="0" max="360" defaultValue="330" onChange={changeangle} step="10"/>
            <Sketch setup={setup} draw={draw} />
        </div>
    )
}
